  }
  .portfolio_item h3{
   margin:1.2rem 0 2rem;
  }
  .portfolio_item-cta{
    display:flex;
   grid-gap:1em;
   gap:1em;
   margin-bottom:1rem;
  }

  @media screen and(max-width:1024px){
    .portfolio_container{
    
        grid-template-columns:1fr 1fr;
        grid-gap:1.2rem;
        gap:1.2rem;
    } 

 }

 @media screen and(max-width:600px){
    portfolio_container{
     grid-template-columns:1fr;
